import React from "react";
import { withRouter } from "next/router";
import AnimationLinks from "../components/AnimationLinks";
import MainLayout from "../components/MainLayout";
import { animationLinks } from "../js/globals";

export default MainLayout(
  withRouter(
    class Animation extends React.Component {
      render() {
        const showComponent = () => {
          return animationLinks.map(link => {
            const nameLength = link.name.split("").length;
            const linkName =
              link.name.split("")[0].toUpperCase() +
              link.name
                .split("")
                .splice(1, nameLength)
                .join("");
            const Component = require("../components/animation/" + linkName)
              .default;
            return <Component />;
          });
        };
        return (
          <div>
            {showComponent()}
            <AnimationLinks />
          </div>
        );
      }
    }
  )
);
